import React, { PropsWithChildren } from 'react'
import styled, { css } from 'styled-components'
import { createSquareSizeStyles } from '../utils'
import { media, color } from '../Settings'
import Svg, { SvgIconType } from '../Svg'

const Wrap = styled.div`
  display: inline-block;
`

const logoSizeStyles = {
  lg: css`
    ${createSquareSizeStyles('8rem')}
    ${media.minSm} {
      ${createSquareSizeStyles('9.5rem')}
    }
    ${media.minMd} {
      ${createSquareSizeStyles('10rem')}
    }
  `,
  md: css`
    ${createSquareSizeStyles('6rem')}
    ${media.minSm} {
      ${createSquareSizeStyles('7rem')}
    }
    ${media.minMd} {
      ${createSquareSizeStyles('8rem')}
    }
  `,
}

type PropType = {
  icon: SvgIconType
  size?: keyof typeof logoSizeStyles
}

const Logo = (props: PropsWithChildren<PropType>) => {
  const { icon, size = 'md' } = props
  const sizeCss = logoSizeStyles[size]

  return (
    <Wrap css={sizeCss}>
      <Svg icon={icon} color={color.brand} />
    </Wrap>
  )
}

export default Logo
