import React from 'react'
import styled, { css } from 'styled-components'
import { darken } from 'polished'
import { color } from '../../Settings'

const progressHeightStyles = css`
  height: 2.4rem;
`

export const ProgressWrap = styled.div`
  ${progressHeightStyles};
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-top: 1rem;
`

export const ProgressPlaceholder = styled.div`
  ${progressHeightStyles};
`

const ProgressBar = styled.div`
  background-color: ${darken(0.01, color.rome)};
  width: calc(100% - 4rem);
  max-width: 270px;
  position: relative;
  height: 0.4rem;
  overflow: hidden;
  border-radius: 0.2rem;
`

const ProgressHighlight = styled.div`
  position: absolute;
  background-color: ${color.brand};
  width: 100%;
  top: 0;
  bottom: 0;
  left: -100%;
`

type PropType = {
  progress: number
}

export const Progress = (props: PropType) => {
  const { progress } = props

  return (
    <ProgressWrap>
      <ProgressBar>
        <ProgressHighlight
          style={{
            transform: `translateX(${progress * 100}%)`,
            WebkitTransform: `translateX(${progress * 100}%)`,
          }}
        />
      </ProgressBar>
    </ProgressWrap>
  )
}
