import { EmblaOptions } from 'embla-carousel'
import styled, { css } from 'styled-components'
import { color, media } from '../../Settings'

type PropType = {
  axis?: EmblaOptions['axis']
}

const sceneDefaultHeightStyles = css`
  height: 18.5rem;

  ${media.minSm} {
    height: 21.5rem;
  }
  ${media.minMd} {
    height: 23rem;
  }
`

export const SceneDefault = styled.div`
  ${sceneDefaultHeightStyles};
  background-color: ${color.rome};
  position: relative;
  padding: 2rem;

  > div:first-child {
    height: 100%;
  }
`

export const SceneDefaultContainer = styled.div<PropType>`
  display: flex;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -webkit-tap-highlight-color: transparent;
  flex-wrap: ${({ axis }) => (axis === 'y' ? 'wrap' : 'nowrap')};
  margin-left: -0.6rem;
  margin-top: -0.6rem;
  height: calc(100% + 0.6rem);

  ${media.minSm} {
    height: calc(100% + 0.8rem);
    margin-left: -0.8rem;
    margin-top: -0.8rem;
  }
  ${media.minMd} {
    height: calc(100% + 1rem);
    margin-left: -1rem;
    margin-top: -1rem;
  }
`

export const SceneDefaultPlaceholder = styled.div`
  ${sceneDefaultHeightStyles};
`
