import { transition, color, media } from '../Settings'
import { css } from 'styled-components'
import { createSquareSizeStyles } from '../utils'

type PropType = {
  defaultColor?: string
  hoverColor?: string
}

export const linkStyles = css<PropType>`
  outline: 0;
  text-decoration: none;
  color: ${({ defaultColor }) => defaultColor || 'inherit'};
  ${media.minLg} {
    transition: color ${transition.default};
    &:hover {
      color: ${({ hoverColor }) => hoverColor || color.brand};
    }
  }
`

export const linkIconStyles = css`
  font-size: 0;
  display: block;

  ${createSquareSizeStyles('1.5rem')}

  ${media.minSm} {
    ${createSquareSizeStyles('1.7rem')}
  }
  ${media.minMd} {
    ${createSquareSizeStyles('1.8rem')}
  }
`
