import React from 'react'
import { EmblaOptions } from 'embla-carousel'
import { useEmblaCarousel } from 'embla-carousel/react'
import { PrevButton, NextButton } from './NavButton'
import { useInView } from 'react-intersection-observer'
import { useEmblaUtils } from '../hook'
import { Dots, DotButton, DotsPlaceholder } from './Dots'
import { SlideDefault } from './SlideDefault'
import { transition } from '../../Settings'
import { css } from 'styled-components'
import {
  SceneDefault,
  SceneDefaultContainer,
  SceneDefaultPlaceholder,
} from './SceneDefault'

type PropType = {
  slides: number[]
  showDots?: boolean
  options?: EmblaOptions
}

const Carousel = (props: PropType) => {
  const { slides, showDots = false, options = {} } = props
  const [EmblaCarousel, embla] = useEmblaCarousel({
    ...options,
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })
  const {
    scrollTo,
    scrollPrev,
    scrollNext,
    prevBtnEnabled,
    nextBtnEnabled,
    scrollSnaps,
    selectedIndex,
  } = useEmblaUtils(embla)

  return (
    <>
      <SceneDefault>
        <EmblaCarousel>
          <SceneDefaultContainer axis={options.axis}>
            {slides.map((width, index) => (
              <SlideDefault key={index} width={width}>
                {index + 1}
              </SlideDefault>
            ))}
          </SceneDefaultContainer>
        </EmblaCarousel>
        <PrevButton onClick={scrollPrev} enabled={prevBtnEnabled} />
        <NextButton onClick={scrollNext} enabled={nextBtnEnabled} />
      </SceneDefault>
      {showDots && (
        <Dots>
          {scrollSnaps.map((_, index) => (
            <DotButton
              key={index}
              selected={index === selectedIndex}
              onClick={() => scrollTo(index)}
            />
          ))}
        </Dots>
      )}
    </>
  )
}

const CarouselWithPlaceholder = (props: PropType) => {
  const { showDots } = props
  const [ref, inView] = useInView({ triggerOnce: true })

  return (
    <div
      ref={ref}
      css={css`
        transition: opacity ${transition.default};
        opacity: ${inView ? 1 : 0};
      `}
    >
      {inView ? (
        <Carousel {...props} />
      ) : (
        <>
          <SceneDefaultPlaceholder />
          {showDots && <DotsPlaceholder />}
        </>
      )}
    </div>
  )
}

export default CarouselWithPlaceholder
