import React from 'react'
import { createSquareSizeStyles } from '../utils'
import { color, media } from '../Settings'
import { useNavigation } from '../Navigation'
import styled from 'styled-components'
import { Link } from '../Link'
import Svg from '../Svg'

const Wrap = styled(Link)`
  display: flex;
  align-items: center;
  text-decoration: none;
  color: ${color.prague};
  &:hover {
    color: ${color.prague};
  }
  font-size: 1.6rem;

  ${media.minSm} {
    font-size: 1.9rem;
  }
  ${media.minMd} {
    font-size: 2rem;
  }
`

const LogoType = styled.div`
  margin-right: 1rem;
  ${createSquareSizeStyles('1.8rem')}

  ${media.minSm} {
    ${createSquareSizeStyles('2.1rem')}
  }
  ${media.minMd} {
    ${createSquareSizeStyles('2.3rem')}
  }
`

const Logo = () => {
  const { onNavItemClick } = useNavigation()

  return (
    <Wrap onClick={onNavItemClick} to="/">
      <LogoType>
        <Svg icon="emblaLogo" color={color.brand} />
      </LogoType>
      <span>Embla Carousel</span>
    </Wrap>
  )
}

export default Logo
