import React, { PropsWithChildren } from 'react'
import { media } from '../Settings'
import styled from 'styled-components'

export const FRAME_MAX_WIDTH = '1000px'
export const FRAME_SPACING = '2rem'

export const Wrap = styled.div`
  max-width: ${FRAME_MAX_WIDTH};
  width: 100%;
  padding-left: ${FRAME_SPACING};
  padding-right: ${FRAME_SPACING};
  margin-left: auto;
  margin-right: auto;

  ${media.minSm} {
    width: 95%;
  }
  ${media.minMd} {
    width: 90%;
  }
`

export const WrapCollapse = styled.div`
  ${media.maxXs} {
    width: calc(100% + ${FRAME_SPACING} * 2);
    margin-left: -${FRAME_SPACING};
    margin-right: -${FRAME_SPACING};
  }
`

const Frame = (props: PropsWithChildren<{}>) => {
  const { children, ...restProps } = props
  return <Wrap {...restProps}>{children}</Wrap>
}

export default Frame
