import { NavHashLinkProps } from 'react-router-hash-link'
import React, { PropsWithChildren } from 'react'
import { useTabAccess, tabAccessStyles } from '../TabAccess'
import { TabAccessType } from '../TabAccess/TabAccessStyles'
import styled, { css } from 'styled-components'
import { Link } from '../Link'
import {
  ButtonSizeType,
  plainButtonStyles,
  buttonSizeStyles,
} from './ButtonStyles'

export type PropType = TabAccessType & ButtonSizeType & NavHashLinkProps

const Wrap = styled(Link)`
  ${plainButtonStyles}
`

export const ButtonLink = (props: PropsWithChildren<PropType>) => {
  const { children, size = '', tabAccessType = 'outer', ...restProps } = props
  const sizeCss = size ? buttonSizeStyles[size] : ''
  const tabAccessCss = tabAccessStyles[tabAccessType]
  const tabAccess = useTabAccess()

  return (
    <Wrap
      {...restProps}
      css={css`
        ${sizeCss};
        ${tabAccess ? tabAccessCss : ''};
      `}
    >
      {children}
    </Wrap>
  )
}

export default ButtonLink
