import React, { useEffect, useCallback, useState } from 'react'
import { EmblaOptions } from 'embla-carousel'
import { useEmblaCarousel } from 'embla-carousel/react'
import { SlideDefault } from './SlideDefault'
import { SlideThumbnail } from './SlideThumbnail'
import { useInView } from 'react-intersection-observer'
import { css } from 'styled-components'
import { transition } from '../../Settings'
import { Color } from '../../Text'
import {
  SceneThumbnail,
  SceneThumbnailContainer,
  SceneThumbnailPlaceholder,
} from './SceneThumbnail'
import {
  SceneDefault,
  SceneDefaultContainer,
  SceneDefaultPlaceholder,
} from './SceneDefault'

type PropType = {
  slides: number[]
  options?: EmblaOptions
}

const Carousel = (props: PropType) => {
  const { slides, options = {} } = props
  const [EmblaCarousel, embla] = useEmblaCarousel({
    ...options,
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })
  const [EmblaCarouselThumbs, emblaThumbs] = useEmblaCarousel({
    containScroll: 'keepSnaps',
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })
  const [selectedIndex, setSelectedIndex] = useState(0)

  const onThumbClick = useCallback(
    (index: number) => {
      if (!embla || !emblaThumbs) return
      if (emblaThumbs.clickAllowed()) {
        embla.scrollTo(index)
      }
    },
    [embla, emblaThumbs],
  )

  const onSelect = useCallback(() => {
    if (!embla || !emblaThumbs) return
    setSelectedIndex(embla.selectedScrollSnap())
    emblaThumbs.scrollTo(embla.selectedScrollSnap())
  }, [embla, emblaThumbs, setSelectedIndex])

  useEffect(() => {
    if (!embla) return
    embla.on('select', onSelect)
    onSelect()
  }, [embla, onSelect])

  return (
    <>
      <SceneDefault>
        <EmblaCarousel>
          <SceneDefaultContainer>
            {slides.map((width, index) => (
              <SlideDefault key={index} width={width}>
                {index + 1}
              </SlideDefault>
            ))}
          </SceneDefaultContainer>
        </EmblaCarousel>
      </SceneDefault>
      <SceneThumbnail>
        <EmblaCarouselThumbs>
          <SceneThumbnailContainer>
            {slides.map((_, index) => (
              <SlideThumbnail
                onClick={() => onThumbClick(index)}
                key={index}
                width={20}
              >
                <Color color={index === selectedIndex ? 'brand' : 'prague'}>
                  {index + 1}
                </Color>
              </SlideThumbnail>
            ))}
          </SceneThumbnailContainer>
        </EmblaCarouselThumbs>
      </SceneThumbnail>
    </>
  )
}

const CarouselWithPlaceholder = (props: PropType) => {
  const [ref, inView] = useInView({ triggerOnce: true })

  return (
    <div
      ref={ref}
      css={css`
        transition: opacity ${transition.default};
        opacity: ${inView ? 1 : 0};
      `}
    >
      {inView ? (
        <Carousel {...props} />
      ) : (
        <>
          <SceneDefaultPlaceholder />
          <SceneThumbnailPlaceholder />
        </>
      )}
    </div>
  )
}

export default CarouselWithPlaceholder
