import React, { useCallback, useEffect } from 'react'
import { useEmblaUtils } from '../hook'
import { EmblaOptions } from 'embla-carousel'
import { useEmblaCarousel } from 'embla-carousel/react'
import { PrevButton, NextButton } from './NavButton'
import { useInterval } from '../../utils'
import { SlideDefault } from './SlideDefault'
import { useInView } from 'react-intersection-observer'
import { transition } from '../../Settings'
import { css } from 'styled-components'
import {
  SceneDefault,
  SceneDefaultContainer,
  SceneDefaultPlaceholder,
} from './SceneDefault'

type PropType = {
  slides: number[]
  options?: EmblaOptions
}

const Carousel = (props: PropType) => {
  const { slides, options = {} } = props
  const [EmblaCarousel, embla] = useEmblaCarousel({
    ...options,
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })
  const {
    scrollPrev,
    scrollNext,
    autoplay,
    prevBtnEnabled,
    nextBtnEnabled,
  } = useEmblaUtils(embla)

  const { play, stop } = useInterval(autoplay, 4000)

  const scrollNextAutoplay = useCallback(() => {
    scrollNext()
    stop()
  }, [scrollNext, stop])

  const scrollPrevAutoplay = useCallback(() => {
    scrollPrev()
    stop()
  }, [scrollPrev, stop])

  useEffect(() => {
    play()
  }, [play])

  useEffect(() => {
    if (!embla) return
    embla.on('pointerDown', stop)
  }, [embla, stop])

  return (
    <SceneDefault>
      <EmblaCarousel>
        <SceneDefaultContainer>
          {slides.map((width, index) => (
            <SlideDefault key={index} width={width}>
              {index + 1}
            </SlideDefault>
          ))}
        </SceneDefaultContainer>
      </EmblaCarousel>
      <PrevButton onClick={scrollPrevAutoplay} enabled={prevBtnEnabled} />
      <NextButton onClick={scrollNextAutoplay} enabled={nextBtnEnabled} />
    </SceneDefault>
  )
}

const CarouselWithPlaceholder = (props: PropType) => {
  const [ref, inView] = useInView({ triggerOnce: true })

  return (
    <div
      ref={ref}
      css={css`
        transition: opacity ${transition.default};
        opacity: ${inView ? 1 : 0};
      `}
    >
      {inView ? <Carousel {...props} /> : <SceneDefaultPlaceholder />}
    </div>
  )
}

export default CarouselWithPlaceholder
