import React from 'react'
import { NavigationProvider } from './Navigation'
import { TabAccessProvider } from './TabAccess'
import { Switch, Route } from 'react-router-dom'
import { RouteChangeScroll, RouteInitScroll } from './Router'
import Header from './Header'
import GlobalStyles from './GlobalStyles'
import Footer from './Footer'
import Examples from './Pages/Examples'
import Home from './Pages/Home'
import Api from './Pages/Api'

function App() {
  return (
    <>
      <GlobalStyles />
      <TabAccessProvider>
        <NavigationProvider>
          <Header />
          <Switch>
            <Route path="/examples">
              <Examples />
            </Route>
            <Route path="/api">
              <Api />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
          <Footer />
        </NavigationProvider>
      </TabAccessProvider>
      <RouteInitScroll />
      <RouteChangeScroll />
    </>
  )
}

export default App
