import React, {
  createContext,
  useState,
  PropsWithChildren,
  useEffect,
  useCallback,
} from 'react'

export const TabAccessContext = createContext(false)

export const TabAccessProvider = (props: PropsWithChildren<{}>) => {
  const { children } = props
  const [userIsTabbing, setUserIsTabbing] = useState(false)

  const onMouseDown = useCallback(() => {
    setUserIsTabbing(false)
    document.removeEventListener('mousedown', onMouseDown)
  }, [setUserIsTabbing])

  const onKeyup = useCallback(
    (event: KeyboardEvent) => {
      if (event.keyCode === 9) {
        setUserIsTabbing(true)
        document.addEventListener('mousedown', onMouseDown)
      }
    },
    [setUserIsTabbing, onMouseDown],
  )

  useEffect(() => {
    document.addEventListener('keyup', onKeyup)
    return () => {
      document.removeEventListener('keyup', onKeyup)
      document.removeEventListener('mousedown', onMouseDown)
    }
  })

  return (
    <TabAccessContext.Provider value={userIsTabbing}>
      {children}
    </TabAccessContext.Provider>
  )
}
