import React, { PropsWithChildren } from 'react'
import styled, { css } from 'styled-components'
import { useTabAccess, tabAccessStyles } from '../TabAccess'
import { TabAccessType } from '../TabAccess/TabAccessStyles'
import {
  plainButtonStyles,
  ButtonSizeType,
  buttonSizeStyles,
} from './ButtonStyles'

const Wrap = styled.button`
  ${plainButtonStyles}
`

export type PropType = ButtonSizeType &
  TabAccessType &
  PropsWithChildren<React.ButtonHTMLAttributes<HTMLButtonElement>>

export const Button = (props: PropsWithChildren<PropType>) => {
  const { children, size = '', tabAccessType = 'outer', ...restProps } = props
  const sizeCss = size ? buttonSizeStyles[size] : ''
  const tabAccessCss = tabAccessStyles[tabAccessType]
  const tabAccess = useTabAccess()

  return (
    <Wrap
      {...restProps}
      css={css`
        ${sizeCss};
        ${tabAccess ? tabAccessCss : ''};
      `}
    >
      {children}
    </Wrap>
  )
}

export default Button
