import React from 'react'
import styled from 'styled-components'
import { Link, ExternalLink, linkIconStyles } from '../Link'
import { color, media } from '../Settings'
import { scrollToElement } from '../utils'
import Svg from '../Svg'

const Wrap = styled.div`
  display: flex;
  margin-left: -3rem;
  ${media.maxXs} {
    > *:first-child {
      display: none;
    }
  }
`

const LinkWithSvg = styled(ExternalLink)`
  margin-left: 3rem;
  display: flex;
  align-items: center;
`
const LinkSvg = styled.span`
  ${linkIconStyles}
  margin-left: 1rem;
`

const Links = () => {
  return (
    <Wrap>
      <Link scroll={scrollToElement} to="/examples">
        Examples
      </Link>
      <LinkWithSvg href="https://github.com/davidcetinkaya/embla-carousel">
        Github
        <LinkSvg>
          <Svg icon="githubLogo" color={color.riga} />
        </LinkSvg>
      </LinkWithSvg>
    </Wrap>
  )
}

export default Links
