import React, { PropsWithChildren } from 'react'
import styled from 'styled-components'
import { itemStyles } from './TableStyles'
import { color } from '../Settings'

const Wrap = styled.table`
  display: block;
  overflow: auto;
  border-spacing: 0;
  border-collapse: collapse;
  text-align: left;
  width: 100%;
  font-size: 1.4rem;
  tr:nth-child(2n) {
    background-color: ${color.rome};
  }
`

const Heading = styled.th`
  ${itemStyles}
`

type PropType = {
  keyHeading: string
  valueHeading: string
}

const KeyValueTable = (props: PropsWithChildren<PropType>) => {
  const { keyHeading, valueHeading, children } = props

  return (
    <Wrap>
      <thead>
        <tr>
          <Heading>{keyHeading}</Heading>
          <Heading>{valueHeading}</Heading>
        </tr>
      </thead>
      <tbody>{children}</tbody>
    </Wrap>
  )
}

export default KeyValueTable
