import styled from 'styled-components'
import { lighten, rgba } from 'polished'
import { color, media } from '../Settings'

export const Wrap = styled.div`
  background-color: ${color.rome};
  overflow: hidden;
  position: relative;
  &::before,
  &::after {
    content: '';
    position: absolute;
    top: -1px;
    bottom: -1px;
    padding-left: 3rem;
    pointer-events: none;
    user-select: none;
  }
  &::before {
    left: 0;
    background: linear-gradient(
      to left,
      ${rgba(color.rome, 0)} 0%,
      ${rgba(color.rome, 1)} 100%
    );
  }
  &::after {
    right: 0;
    background: linear-gradient(
      to right,
      ${rgba(color.rome, 0)} 0%,
      ${rgba(color.rome, 1)} 100%
    );
  }
`

export const Highlight = styled.div`
  width: 100%;
  overflow: hidden;
  background-color: ${color.rome};
  code {
    color: ${color.brand};
  }
  pre {
    overflow: auto;
    margin-bottom: 0;
    ::-webkit-scrollbar-thumb {
      background-color: ${lighten(0.125, color.brand)};
    }
    ::-webkit-scrollbar-track {
      background-color: ${lighten(0.4, color.brand)};
    }
    ::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }
    code {
      text-align: left;
      white-space: pre;
      word-spacing: normal;
      word-break: normal;
      word-wrap: normal;
      line-height: 1.375;
      tab-size: 2;
      -moz-tab-size: 2; /* Autoprefixer helper */
      hyphens: none;
      background-color: transparent;
      margin: 0;
      padding: 2.25rem;
      overflow: initial;
      float: left;
      min-width: 100%;
      color: ${color.riga};
      font-size: 1.1rem;

      ${media.minSm} {
        font-size: 1.2rem;
      }

      ${media.minMd} {
        font-size: 1.3rem;
      }
    }
    .token.comment,
    .token.prolog,
    .token.doctype,
    .token.cdata {
      color: #8a8a8a;
    }
    .token.class-name,
    .token.boolean,
    .token.number,
    .token.property,
    .token.tag,
    .token.constant,
    .token.attr-value,
    .token.keyword,
    .token.control,
    .token.directive,
    .token.unit {
      color: ${color.madrid};
    }
    .token.string,
    .token.function:not(.function-variable),
    .token.selector,
    .token.attr-name,
    .token.entity,
    .token.url,
    .language-css .token.string,
    .style .token.string,
    .token.statement,
    .token.regex,
    .token.atrule,
    .token.placeholder,
    .token.variable {
      color: ${color.brand};
    }
    .token.namespace {
      opacity: 0.75;
    }
    .token.deleted {
      text-decoration: line-through;
    }
    .token.italic {
      font-style: italic;
    }
    .token.important,
    .token.bold {
      font-weight: 600;
    }
    .token.entity {
      cursor: help;
    }
  }
`
