import React from 'react'
import { createSquareSizeStyles } from '../../utils'
import styled, { css } from 'styled-components'
import { color, zindex, media } from '../../Settings'
import { Button } from '../../Button'
import Svg from '../../Svg'

const NavButton = styled(Button)`
  z-index: ${zindex.step};
  color: ${color.brand};
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  &:disabled {
    cursor: default;
    opacity: 0.3;
  }
  ${createSquareSizeStyles('2.5rem')}

  ${media.minSm} {
    ${createSquareSizeStyles('2.7rem')}
  }
  ${media.minMd} {
    ${createSquareSizeStyles('3rem')}
  }
`

const NavButtonSvg = styled(Svg)`
  ${createSquareSizeStyles('3.5rem')}
`

type PropType = {
  enabled: boolean
  onClick: () => void
}

export const PrevButton = (props: PropType) => {
  const { enabled, onClick } = props

  return (
    <NavButton
      onClick={onClick}
      disabled={!enabled}
      css={css`
        left: 2.7rem;
      `}
    >
      <NavButtonSvg icon="arrowLeft" />
    </NavButton>
  )
}

export const NextButton = (props: PropType) => {
  const { enabled, onClick } = props

  return (
    <NavButton
      onClick={onClick}
      disabled={!enabled}
      css={css`
        right: 2.7rem;
      `}
    >
      <NavButtonSvg icon="arrowRight" />
    </NavButton>
  )
}
