import React, { PropsWithChildren } from 'react'
import styled, { css } from 'styled-components'
import { H2Style } from '../../Text'
import { media, brandGradient, color } from '../../Settings'
import { Button } from '../../Button'

const SlideThumbnailWrap = styled.div`
  ${H2Style};
  position: relative;
  height: 100%;
  padding-left: 0.3rem;
  cursor: pointer;

  ${media.minSm} {
    padding-left: 0.6rem;
  }
  ${media.minMd} {
    padding-left: 0.8rem;
  }
`

const SlideThumbnailInner = styled(Button)`
  ${brandGradient};
  color: ${color.prague};
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  height: 100%;
  width: 100%;
`

type PropType = React.HTMLAttributes<HTMLDivElement> & {
  onClick: () => void
  width: number
}

export const SlideThumbnail = (props: PropsWithChildren<PropType>) => {
  const { width, children, onClick, ...restProps } = props

  return (
    <SlideThumbnailWrap
      {...restProps}
      css={css`
        min-width: ${width}%;
      `}
    >
      <SlideThumbnailInner onClick={onClick} tabAccessType="inner">
        {children}
      </SlideThumbnailInner>
    </SlideThumbnailWrap>
  )
}
