import React, {
  createContext,
  PropsWithChildren,
  useReducer,
  Dispatch,
} from 'react'

export type StateType = boolean
type ActionType = { type: 'open' | 'close' }

type ContextType = {
  state: StateType
  dispatch: Dispatch<ActionType>
}

export const NavigationContext = createContext<ContextType>({
  state: false,
  dispatch: () => {},
})

const reducer = (_: StateType, action: ActionType) => {
  switch (action.type) {
    case 'open':
      return true
    case 'close':
      return false
    default:
      return false
  }
}

export const NavigationProvider = (props: PropsWithChildren<{}>) => {
  const { children } = props
  const [state, dispatch] = useReducer(reducer, false)

  return (
    <NavigationContext.Provider value={{ state, dispatch }}>
      {children}
    </NavigationContext.Provider>
  )
}
