import { css, FlattenSimpleInterpolation } from 'styled-components'
import { HEADER_HEIGHT_MIN_LG } from './Header'
import { useRef, useEffect, useCallback, useState } from 'react'

export const createSquareSizeStyles = (
  size: string,
): FlattenSimpleInterpolation => {
  return css`
    width: ${size};
    height: ${size};
  `
}

export const scrollToElement = (element: HTMLElement): void => {
  const yOffset = parseInt(HEADER_HEIGHT_MIN_LG) * 10 + 5
  const y = element.getBoundingClientRect().top + window.pageYOffset - yOffset
  window.scrollTo({ top: y })
}

export const useInterval = (callback: () => void, delay: number) => {
  const [isRunning, setIsRunning] = useState(false)
  const savedCallback = useRef(callback)
  const stop = useCallback(() => setIsRunning(false), [setIsRunning])
  const play = useCallback(() => setIsRunning(true), [setIsRunning])

  useEffect(() => {
    savedCallback.current = callback
  }, [callback])

  useEffect(() => {
    if (!isRunning) return
    let id = 0

    const tick = () => {
      if (!isRunning) return clearTimeout(id)
      savedCallback.current()
      requestAnimationFrame(() => (id = setTimeout(tick, delay)))
    }
    requestAnimationFrame(() => (id = setTimeout(tick, delay)))

    return () => {
      if (id) clearTimeout(id)
      stop()
    }
  }, [isRunning, delay, stop])

  return { play, stop }
}
