import React, { PropsWithChildren } from 'react'
import styled, { css } from 'styled-components'
import { brandGradient, color, media } from '../../Settings'
import { H1Style } from '../../Text'

const SlideDefaultWrap = styled.div`
  ${H1Style};
  position: relative;
  height: 100%;
  padding-left: 0.6rem;
  padding-top: 0.6rem;

  ${media.minSm} {
    padding-left: 0.8rem;
    padding-top: 0.8rem;
  }
  ${media.minMd} {
    padding-left: 1rem;
    padding-top: 1rem;
  }
`

const SlideDefaultInner = styled.div`
  ${brandGradient};
  color: ${color.prague};
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  height: 100%;
  width: 100%;
`

type PropType = React.HTMLAttributes<HTMLDivElement> & {
  width: number
}

export const SlideDefault = (props: PropsWithChildren<PropType>) => {
  const { width, children, ...restProps } = props

  return (
    <SlideDefaultWrap
      {...restProps}
      css={css`
        min-width: ${width}%;
      `}
    >
      <SlideDefaultInner>{children}</SlideDefaultInner>
    </SlideDefaultWrap>
  )
}
