import React from 'react'
import { createSquareSizeStyles } from '../../utils'
import styled, { css } from 'styled-components'
import { color } from '../../Settings'
import { Button } from '../../Button'
import { darken } from 'polished'

const SELECTED_COLOR = color.brand
const IDLE_COLOR = darken(0.01, color.rome)

const dotsHeightStyles = css`
  height: 2.4rem;
`

export const Dots = styled.div`
  ${dotsHeightStyles};
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
`

export const DotsPlaceholder = styled.div`
  ${dotsHeightStyles};
`

export const Dot = styled(Button)`
  ${createSquareSizeStyles('3rem')}
  background-color: transparent;
  display: flex;
  align-items: center;
  margin-top: 1rem;
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  &:after {
    border-radius: 0.2rem;
    width: 100%;
    height: 0.4rem;
    content: '';
  }
`

type PropType = {
  selected: boolean
  onClick: () => void
}

export const DotButton = (props: PropType) => {
  const { selected, onClick } = props

  return (
    <Dot
      onClick={onClick}
      css={css`
        &:after {
          background-color: ${selected ? SELECTED_COLOR : IDLE_COLOR};
        }
      `}
    />
  )
}
