import React, { ReactNode, PropsWithChildren } from 'react'
import styled from 'styled-components'
import { itemStyles } from './TableStyles'
import { media } from '../Settings'

const Wrap = styled.tr`
  vertical-align: baseline;
  &:nth-child(even) {
    background-color: red;
  }
`

const Value = styled.td`
  ${itemStyles}
  ${media.maxXs} {
    font-size: 1.2rem;
  }
`

const Key = styled.td`
  ${itemStyles}
  ${media.maxXs} {
    font-size: 1rem;
  }
`

type PropType = {
  rowKey: string
  children: ReactNode
}

const KeyValueTableRow = (props: PropsWithChildren<PropType>) => {
  const { rowKey, children: value } = props

  return (
    <Wrap>
      <Key>{rowKey}</Key>
      <Value>{value}</Value>
    </Wrap>
  )
}

export default KeyValueTableRow
