import React from 'react'
import emblaLogo from './svgs/embla-logo'
import thinCross from './svgs/thin-cross'
import githubLogo from './svgs/github-logo'
import npmLogo from './svgs/npm-logo'
import curlyBraces from './svgs/curly-braces'
import arrowBraces from './svgs/arrow-braces'
import arrowLeft from './svgs/arrow-left'
import arrowRight from './svgs/arrow-right'

export type SvgIconType = keyof typeof svgList

const svgList = {
  arrowLeft,
  arrowRight,
  curlyBraces,
  arrowBraces,
  npmLogo,
  githubLogo,
  emblaLogo,
  thinCross,
}

export type SvgType = {
  viewbox: string
  body: JSX.Element
}

type PropType = {
  icon: SvgIconType
  width?: string
  height?: string
  color?: string
}

const Svg = (props: PropType) => {
  const {
    icon,
    width = '100%',
    height = '100%',
    color = 'currentColor',
  } = props
  const { viewbox, body } = svgList[icon]

  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      viewBox={viewbox}
      width={width}
      height={height}
      fill={color}
    >
      {body}
    </svg>
  )
}

export default Svg
