import React, { PropsWithChildren } from 'react'
import styled, { css } from 'styled-components'
import { media, brandGradient, color, transition, zindex } from '../Settings'
import { visuallyHiddenStyles } from '../VisuallyHiddenStyles'
import { createSquareSizeStyles } from '../utils'
import { PropType } from '../Button/Button'
import { Button } from '../Button'
import { useNavigation } from '.'
import Svg from '../Svg'

const Wrap = styled(Button)`
  position: fixed;
  z-index: ${zindex.navigation + zindex.step};
  border-radius: 50%;
  bottom: 2rem;
  right: 2rem;
  -webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%);
  ${brandGradient}
  ${media.minSm} {
    display: none;
  }

  ${createSquareSizeStyles('5rem')}
`
const Cross = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 0;
  color: ${color.prague};
  transition: ${transition.default};
  ${createSquareSizeStyles('1.2rem')}
`

const HelperText = styled.span`
  ${visuallyHiddenStyles}
`

const NavButton = (props: PropsWithChildren<PropType>) => {
  const { isOpen } = useNavigation()
  const rotate = isOpen ? 0 : 45
  const translate = `translate(-50%, -50%) rotate(${rotate}deg);`

  return (
    <Wrap {...props}>
      <Cross
        css={css`
          transform: ${translate};
        `}
      >
        <HelperText>Toggle navigation open</HelperText>
        <Svg icon="thinCross" />
      </Cross>
    </Wrap>
  )
}

export default NavButton
