import React from 'react'
import { media, color, zindex } from '../Settings'
import styled, { css } from 'styled-components'
import { Frame } from '../PageLayout'
import Links from './Links'
import Logo from './Logo'

export const HEADER_HEIGHT_DEFAULT = '5rem'
export const HEADER_HEIGHT_MIN_LG = '6rem'

const HEIGHT = css`
  position: relative;
  height: ${HEADER_HEIGHT_DEFAULT};

  ${media.minMd} {
    height: ${HEADER_HEIGHT_MIN_LG};
  }
`

const Wrap = styled.header`
  ${HEIGHT};
`

const Fixed = styled.div`
  ${HEIGHT};
  position: fixed;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  z-index: ${zindex.header};
  background-color: ${color.lisbon};
`

const Content = styled(Frame)`
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
  color: ${color.prague};
`

const Header = () => {
  return (
    <Wrap>
      <Fixed>
        <Content>
          <Logo />
          <Links />
        </Content>
      </Fixed>
    </Wrap>
  )
}

export default Header
