import React, { useEffect, useState } from 'react'
import { useEmblaUtils } from '../hook'
import { EmblaOptions } from 'embla-carousel'
import { useEmblaCarousel } from 'embla-carousel/react'
import { PrevButton, NextButton } from './NavButton'
import { SlideDefault } from './SlideDefault'
import { Progress, ProgressPlaceholder } from './Progress'
import { useInView } from 'react-intersection-observer'
import { transition } from '../../Settings'
import { css } from 'styled-components'
import {
  SceneDefault,
  SceneDefaultContainer,
  SceneDefaultPlaceholder,
} from './SceneDefault'

type PropType = {
  slides: number[]
  options?: EmblaOptions
}

const Carousel = (props: PropType) => {
  const { slides, options = {} } = props
  const [EmblaCarousel, embla] = useEmblaCarousel({
    ...options,
    selectedClass: '',
    draggableClass: '',
    draggingClass: '',
  })
  const {
    scrollPrev,
    scrollNext,
    prevBtnEnabled,
    nextBtnEnabled,
  } = useEmblaUtils(embla)
  const [scrollProgress, setScrollProgress] = useState(0)

  useEffect(() => {
    if (!embla) return

    embla.on('scroll', () => {
      const progress = Math.max(0, Math.min(1, embla.scrollProgress()))
      setScrollProgress(progress)
    })
  }, [embla])

  return (
    <>
      <SceneDefault>
        <EmblaCarousel>
          <SceneDefaultContainer>
            {slides.map((width, index) => (
              <SlideDefault key={index} width={width}>
                {index + 1}
              </SlideDefault>
            ))}
          </SceneDefaultContainer>
        </EmblaCarousel>
        <PrevButton onClick={scrollPrev} enabled={prevBtnEnabled} />
        <NextButton onClick={scrollNext} enabled={nextBtnEnabled} />
      </SceneDefault>
      <Progress progress={scrollProgress} />
    </>
  )
}

const CarouselWithPlaceholder = (props: PropType) => {
  const [ref, inView] = useInView({ triggerOnce: true })

  return (
    <div
      ref={ref}
      css={css`
        transition: opacity ${transition.default};
        opacity: ${inView ? 1 : 0};
      `}
    >
      {inView ? (
        <Carousel {...props} />
      ) : (
        <>
          <SceneDefaultPlaceholder />
          <ProgressPlaceholder />
        </>
      )}
    </div>
  )
}

export default CarouselWithPlaceholder
