import React, { PropsWithChildren } from 'react'
import { media, color, brandGradient, zindex } from '../Settings'
import { Frame } from '../PageLayout'
import styled from 'styled-components'

const Wrap = styled.section`
  position: relative;
  overflow: hidden;
`

const Background = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: skewY(-1.5deg);
  transform-origin: top left;
  ${brandGradient}
`

const Content = styled.div`
  color: ${color.prague};
  z-index: ${zindex.step};
  position: relative;
  text-align: center;
  padding-top: 4rem;
  padding-bottom: 5rem;

  ${media.minSm} {
    padding-top: 5.5rem;
    padding-bottom: 7rem;
  }

  ${media.minMd} {
    padding-top: 8rem;
    padding-bottom: 10rem;
  }
`

const Hero = (props: PropsWithChildren<{}>) => {
  const { children } = props
  return (
    <Wrap>
      <Background />
      <Frame>
        <Content>{children}</Content>
      </Frame>
    </Wrap>
  )
}

export default Hero
