import React from 'react'
import { KeyValueTable, KeyValueTableRow } from '../KeyValueTable'
import { H1, H2, Bold, H3, Color } from '../Text'
import { Grid, Section } from '../PageLayout'
import CodeBlock from '../CodeBlock'
import { Hero, Logo } from '../Hero'
import { Helmet } from 'react-helmet'

const TITLE = 'API Reference – Embla Carousel'

const Api = () => {
  return (
    <>
      <Helmet>
        <title>{TITLE}</title>
        <meta name="twitter:title" content={TITLE} />
        <meta property="og:title" content={TITLE} />
      </Helmet>

      <Hero>
        <Logo icon="curlyBraces" />
        <H1>API Reference</H1>
      </Hero>

      <Grid>
        <Section>
          <H2>Usage</H2>
          <p>
            <Bold>embla-carousel</Bold> is made with extensibility in mind. It
            can be extended using its rich API to cover most of the use cases.
          </p>
        </Section>

        <Section>
          <H3 id="options">Options</H3>
          <p>
            <Bold>embla-carousel</Bold> requires a wrapping element with a
            container to initialize properly. Optionally, you can pass a{' '}
            <Bold>configuration object</Bold> as the second argument in order to
            customize the carousel to your liking.
          </p>
        </Section>

        <Section>
          <CodeBlock language="javascript">
            {`const emblaNode = document.getElementById('embla')
const options = { loop: true }

const embla = EmblaCarousel(emblaNode, options)`}
          </CodeBlock>
        </Section>

        <Section>
          <KeyValueTable keyHeading="Option" valueHeading="Description">
            <KeyValueTableRow rowKey="axis">
              <div>
                <div>
                  Type: <Color color="brand">string</Color>
                </div>
                <div>
                  Default: <Color color="madrid">'x'</Color>
                </div>
              </div>
              <div>
                Choose scroll axis between <Bold>'x'</Bold> and <Bold>'y'</Bold>
                .
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="align">
              <div>
                <div>
                  Type: <Color color="brand">number | string</Color>
                </div>
                <div>
                  Default: <Color color="madrid">'center'</Color>
                </div>
              </div>
              <div>
                Align the slides relative to the carousel viewport. Use one of
                the predefined alignments <Bold>'start'</Bold>,{' '}
                <Bold>'center'</Bold> or <Bold>'end'</Bold>. Alternatively,
                provide a number to align the slides.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="slidesToScroll">
              <div>
                <div>
                  Type: <Color color="brand">number</Color>
                </div>
                <div>
                  Default: <Color color="madrid">1</Color>
                </div>
              </div>
              <div>
                Group slides together. Drag interactions, dot navigation, and
                previous/next buttons are mapped to group slides into the given
                number.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="containScroll">
              <div>
                <div>
                  Type: <Color color="brand">string</Color>
                </div>
                <div>
                  Default: <Color color="madrid">''</Color>
                </div>
              </div>
              <div>
                Clear leading and trailing empty space that causes excessive
                scrolling. Use <Bold>'trimSnaps'</Bold> to only use snap points
                that trigger scrolling or <Bold>'keepSnaps'</Bold> to keep them.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="draggable">
              <div>
                <div>
                  Type: <Color color="brand">boolean</Color>
                </div>
                <div>
                  Default: <Color color="madrid">true</Color>
                </div>
              </div>
              <div>
                Enable for scrolling the carousel with mouse and touch
                interactions.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="dragFree">
              <div>
                <div>
                  Type: <Color color="brand">boolean</Color>
                </div>
                <div>
                  Default: <Color color="madrid">false</Color>
                </div>
              </div>
              <div>
                Enable momentum scrolling. The speed and duration of the
                continued scrolling is proportional to how vigorous the scroll
                gesture was.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="loop">
              <div>
                <div>
                  Type: <Color color="brand">boolean</Color>
                </div>
                <div>
                  Default: <Color color="madrid">false</Color>
                </div>
              </div>
              <div>
                Enable infinite looping. Slides need{' '}
                <Bold>position: relative;</Bold> for this to work. Automatically
                falls back to false if slide content isn't enough to loop.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="speed">
              <div>
                <div>
                  Type: <Color color="brand">number</Color>
                </div>
                <div>
                  Default: <Color color="madrid">10</Color>
                </div>
              </div>
              <div>
                Adjust scroll speed when triggered by any of the API methods.
                Higher numbers enables faster scrolling. Drag interactions are
                not affected because speed is then determined by the drag force.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="startIndex">
              <div>
                <div>
                  Type: <Color color="brand">number</Color>
                </div>
                <div>
                  Default: <Color color="madrid">0</Color>
                </div>
              </div>
              <div>
                Set the initial scroll snap position by index. First snap index
                starts at 0.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="selectedClass">
              <div>
                <div>
                  Type: <Color color="brand">string</Color>
                </div>
                <div>
                  Default: <Color color="madrid">'is-selected'</Color>
                </div>
              </div>
              <div>
                Choose classname that will be applied to the selected slides.
                Pass an empty string to opt-out.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="draggableClass">
              <div>
                <div>
                  Type: <Color color="brand">string</Color>
                </div>
                <div>
                  Default: <Color color="madrid">'is-draggable'</Color>
                </div>
              </div>
              <div>
                Choose classname that will be applied to a draggable carousel
                container. Pass an empty string to opt-out.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="draggingClass">
              <div>
                <div>
                  Type: <Color color="brand">string</Color>
                </div>
                <div>
                  Default: <Color color="madrid">'is-dragging'</Color>
                </div>
              </div>
              <div>
                Choose classname that will be applied to the container when
                dragging. Pass an empty string to opt-out.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="inViewThreshold">
              <div>
                <div>
                  Type: <Color color="brand">number</Color>
                </div>
                <div>
                  Default: <Color color="madrid">0.5</Color>
                </div>
              </div>
              <div>
                Choose a fraction representing the percentage portion of a slide
                that needs to be visible in order to be considered in view.{' '}
                <Bold>0.5 equals 50%</Bold>.
              </div>
            </KeyValueTableRow>
          </KeyValueTable>
        </Section>

        <Section>
          <H3 id="methods">Methods</H3>
          <p>
            <Bold>embla-carousel</Bold> exposes a set of useful methods which
            makes it very extensible. Assuming you've stored the carousel
            instance in a variable, a method is called like demonstrated below.
          </p>
        </Section>

        <Section>
          <CodeBlock language="javascript">
            {`const embla = EmblaCarousel(emblaNode)
            
console.log(\`Current snap index is \${embla.selectedScrollSnap()})`}
          </CodeBlock>
        </Section>

        <Section>
          <KeyValueTable keyHeading="Method" valueHeading="Description">
            <KeyValueTableRow rowKey="containerNode">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">HTMLElement</Color>
                </div>
              </div>
              <div>Get the container node that holds the slides.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="slideNodes">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">HTMLElement[]</Color>
                </div>
              </div>
              <div>Get the slide nodes inside the container.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="scrollNext">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">void</Color>
                </div>
              </div>
              <div>Scroll to the next snap point if possible.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="scrollPrev">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">void</Color>
                </div>
              </div>
              <div>Scroll to the previous snap point if possible.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="scrollTo">
              <div>
                <div>
                  Params: <Color color="brand">index(number)</Color>
                </div>
                <div>
                  Return: <Color color="madrid">void</Color>
                </div>
              </div>
              <div>Scroll to a snap point by its unique index.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="canScrollNext">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">boolean</Color>
                </div>
              </div>
              <div>Check the possiblity to scroll to a next snap point.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="canScrollPrev">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">boolean</Color>
                </div>
              </div>
              <div>
                Check the possiblity to scroll to a previous snap point.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="selectedScrollSnap">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">number</Color>
                </div>
              </div>
              <div>Get the index of the selected snap point.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="previousScrollSnap">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">number</Color>
                </div>
              </div>
              <div>Get the index of the previous snap point.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="scrollSnapList">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">number[]</Color>
                </div>
              </div>
              <div>Get an array containing all the snap point positions.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="scrollProgress">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">number</Color>
                </div>
              </div>
              <div>
                Check how far the carousel has scrolled of its scrollable
                length, from 0 to 1. 0.5 = 50%.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="slidesInView">
              <div>
                <div>
                  Params: <Color color="brand">target(boolean)</Color>
                </div>
                <div>
                  Return: <Color color="madrid">number[]</Color>
                </div>
              </div>
              <div>
                Get slide indexes visible in the carousel viewport. Honors the{' '}
                <Bold>inViewThreshold</Bold> option. Target will check slides
                visible at destination.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="slidesNotInView">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">number[]</Color>
                </div>
              </div>
              <div>
                Get slide indexes not visible in the carousel viewport. Honors
                the <Bold>inViewThreshold</Bold> option. Target will check
                slides not visible at destination.
              </div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="clickAllowed">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">boolean</Color>
                </div>
              </div>
              <div>Check if pointer interaction was a static click.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="reInit">
              <div>
                <div>
                  Params: <Color color="brand">options(EmblaOptions)</Color>
                </div>
                <div>
                  Return: <Color color="madrid">void</Color>
                </div>
              </div>
              <div>Re-initialize the carousel after initialization.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="destroy">
              <div>
                <div>
                  Params: <Color color="brand">none</Color>
                </div>
                <div>
                  Return: <Color color="madrid">void</Color>
                </div>
              </div>
              <div>Destroy the carousel instance permanently.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="on">
              <div>
                <div>
                  Params:{' '}
                  <Color color="brand">
                    event(EmblaEvent) callback(Function)
                  </Color>
                </div>
                <div>
                  Return: <Color color="madrid">void</Color>
                </div>
              </div>
              <div>Subscribe to an Embla specific event with a callback.</div>
            </KeyValueTableRow>

            <KeyValueTableRow rowKey="off">
              <div>
                <div>
                  Params:{' '}
                  <Color color="brand">
                    event(EmblaEvent) callback(Function)
                  </Color>
                </div>
                <div>
                  Return: <Color color="madrid">void</Color>
                </div>
              </div>
              <div>Unsubscribe from an Embla specific event.</div>
            </KeyValueTableRow>
          </KeyValueTable>
        </Section>

        <Section>
          <H3 id="events">Events</H3>
          <p>
            <Bold>embla-carousel</Bold> exposes custom events that can be hooked
            on to. Every event comes with the name of the event.
          </p>
        </Section>

        <Section>
          <CodeBlock language="javascript">
            {`const embla = EmblaCarousel(emblaNode)
            
embla.on('select', (eventName) => {
  console.log(\`Embla just triggered \${eventName}!\`)
})`}
          </CodeBlock>
        </Section>

        <Section>
          <KeyValueTable keyHeading="Event" valueHeading="Description">
            <KeyValueTableRow rowKey="init">
              Fires when the carousel mounts for the first time.
            </KeyValueTableRow>
            <KeyValueTableRow rowKey="destroy">
              Fires when the carousel has been destroyed.
            </KeyValueTableRow>
            <KeyValueTableRow rowKey="select">
              Fires when the selected scroll snap changes.
            </KeyValueTableRow>
            <KeyValueTableRow rowKey="scroll">
              Fires when the carousel is scrolling. Remember to throttle this if
              you're doing expensive stuff.
            </KeyValueTableRow>
            <KeyValueTableRow rowKey="settle">
              Fires when the carousel has settled after scroll was triggered.
            </KeyValueTableRow>
            <KeyValueTableRow rowKey="resize">
              Fires when the carousel has resized.
            </KeyValueTableRow>
            <KeyValueTableRow rowKey="pointerDown">
              Fires when the user has pointer down on the carousel.
            </KeyValueTableRow>
            <KeyValueTableRow rowKey="pointerUp">
              Fires when the user has released the pointer from the carousel.
            </KeyValueTableRow>
          </KeyValueTable>
        </Section>
      </Grid>
    </>
  )
}

export default Api
