import React from 'react'
import styled from 'styled-components'
import { ExternalLink } from '../Link'
import { media } from '../Settings'
import { Color } from '../Text'

const Wrap = styled.div`
  display: flex;
  flex-wrap: wrap;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1rem;
  margin-top: -0.4rem;

  ${media.minSm} {
    margin-top: -0.6rem;
  }
  ${media.minMd} {
    margin-top: -0.8rem;
  }
`

const LinkListLabel = styled.span`
  flex: 0 0 auto;
`

const LinkListContent = styled.div`
  display: flex;
  flex-wrap: wrap;
  flex: 1 0 0;
  margin-top: -0.4rem;
  margin-bottom: -0.4rem;
  > a {
    flex: 0 0 auto;
    padding-top: 0.4rem;
    padding-bottom: 0.4rem;
    margin-left: 1rem;
  }

  ${media.minSm} {
    margin-top: -0.6rem;
    margin-bottom: -0.6rem;
    > a {
      padding-top: 0.6rem;
      padding-bottom: 0.6rem;
      margin-left: 1.2rem;
    }
  }
  ${media.minMd} {
    margin-top: -0.8rem;
    margin-bottom: -0.8rem;
    > a {
      padding-top: 0.8rem;
      padding-bottom: 0.8rem;
    }
  }
`

type LinkType = {
  label: string
  href: string
}

type PropType = {
  links: LinkType[]
}

const Links = (props: PropType) => {
  const { links } = props

  return (
    <Wrap>
      <LinkListLabel>Edit code:</LinkListLabel>
      <LinkListContent>
        {links.map(({ href, label }) => (
          <ExternalLink href={href} key={label}>
            <Color color="brand">{label}</Color>
          </ExternalLink>
        ))}
      </LinkListContent>
    </Wrap>
  )
}

export default Links
