import React, { PropsWithChildren } from 'react'
import styled from 'styled-components'
import { Navigation } from '../Navigation'
import { media } from '../Settings'
import Frame from './Frame'

const SIDEBAR_WIDTH_MIN_SM = '200px'
const SIDEBAR_WIDTH_MIN_MD = '250px'

const Wrap = styled(Frame)`
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;

  ${media.minSm} {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
  ${media.minMd} {
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
`

const Main = styled.main`
  flex: 1 0 0;
  max-width: 100%;

  ${media.minSm} {
    max-width: calc(100% - ${SIDEBAR_WIDTH_MIN_SM});
    padding-right: 3rem;
  }
  ${media.minMd} {
    max-width: calc(100% - ${SIDEBAR_WIDTH_MIN_MD});
    padding-right: 4rem;
  }
`

const Nav = styled.nav`
  ${media.minSm} {
    flex: 0 0 ${SIDEBAR_WIDTH_MIN_SM};
  }
  ${media.minMd} {
    flex: 0 0 ${SIDEBAR_WIDTH_MIN_MD};
  }
`

const Grid = (props: PropsWithChildren<{}>) => {
  const { children } = props
  return (
    <Wrap>
      <Main>{children}</Main>
      <Nav>
        <Navigation />
      </Nav>
    </Wrap>
  )
}

export default Grid
