const createMediaWidthRule = (
  minOrMax: 'min' | 'max',
  value: number,
): string => {
  const queryValue = minOrMax === 'max' ? value - 1 : value
  return `@media (${minOrMax}-width: ${queryValue}px)`
}

export const breakpoint = {
  xs: 576,
  sm: 768,
  md: 992,
  lg: 1200,
}

export const media = {
  minXs: createMediaWidthRule('min', breakpoint.xs),
  minSm: createMediaWidthRule('min', breakpoint.sm),
  minMd: createMediaWidthRule('min', breakpoint.md),
  minLg: createMediaWidthRule('min', breakpoint.lg),
  maxXs: createMediaWidthRule('max', breakpoint.xs),
  maxSm: createMediaWidthRule('max', breakpoint.sm),
  maxMd: createMediaWidthRule('max', breakpoint.md),
  maxLg: createMediaWidthRule('max', breakpoint.lg),
}
