import React, { PropsWithChildren } from 'react'
import { HashLink, NavHashLinkProps } from 'react-router-hash-link'
import { TabAccessType } from '../TabAccess/TabAccessStyles'
import { useTabAccess, tabAccessStyles } from '../TabAccess'
import { linkStyles } from './LinkStyles'
import { css } from 'styled-components'

type PropType = TabAccessType & NavHashLinkProps

const Link = (props: PropsWithChildren<PropType>) => {
  const { children, tabAccessType = 'outer', ...restProps } = props
  const tabAccess = useTabAccess()
  const tabAccessCss = tabAccessStyles[tabAccessType]

  return (
    <HashLink
      {...restProps}
      css={css`
        ${linkStyles};
        ${tabAccess ? tabAccessCss : ''};
      `}
    >
      {children}
    </HashLink>
  )
}

export default Link
